.component2 {
    width: 100%;
    height: auto;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-wrap: wrap;
}

.c2-left {
    width: 480px;
    margin: auto;
    padding:  0 2rem;
}

.c2-left p {
    padding: 1rem 0;
    font-size: 16px;
}

.c2-left h1 {
    font-size: 3rem;
    color: black;
}

.c2-left h2 {
    font-size: 1rem;
    color: rgb(198, 134, 24);
    letter-spacing: 5px;
    padding: 1rem 0;
}

.c2-right {
    width: 60%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.c2-card {
    width: 320px;
    height: 260px;
    background-color: rgb(255, 255, 255);
    margin: 1rem;
}

.c2-card h1 {
    padding: 1rem;
}

.c2-card p {
    padding: 1rem;
}

.c2-card-flex {
    display: flex;
    justify-content: center;
}

.c2-card-flex h2 {
    /* margin: auto; */
    background-color: rgb(198, 134, 24);
    padding: 1rem 1.4rem;
    border-radius: 2rem;
    color: white;
    font-size: 1rem;
}

.c2-card img {
    text-align: center;
}

.c2-card-flex h1 {
    font-size: 1.1rem;
}

.c2-right .c2-card-img {
    text-align: center;
    padding: .5rem 0;
}

.c2-link p {
    padding: 0 1rem;
    color: rgb(198, 134, 24);

}

@media  screen and (max-width:1200px) {
    .c2-left {
        width: 100%;
        text-align: center;
    }

    .c2-left h1 {
        font-size: 2rem;
    }

    .c2-right {
        width: 100%;
    }

    .c2-link p {
        padding: 0 1rem;
    }
    
    .c2-card-flex {
        justify-content: center;
    }

}

@media  screen and (max-width: 700px) {
    .c2-card {
        width: 100%;
        text-align: center;
        height: 230px;
    }


}