
.c5 {
    background-color: #F4FCFA;
    padding: 3rem 0;
}
.c5-header {
    text-align: center;
    width: 100%;
    height: auto;
    padding: 3rem 0 5rem 0;
}

.c5-header h1 {
    font-size: 2rem;
    padding: 1rem;
}

.c5-cards {
    display: flex;
    /* flex-wrap: wrap; */
    padding: 3rem 1rem;
    gap: 1rem;
}

.c5-flexItem {
    background-color: rgba(255, 255, 255, 0.673);
    padding: 3rem 2rem;
    width: 35.999%;
    height: auto;
    box-shadow: 0px 10px 122px -52px rgba(173,163,163,0.75);
    border-radius: 2rem;
}



.flexIcon {
    font-size: 5rem;
    color: rgb(200, 112, 17);
    text-align: center;
}

.c5-flexItem h1 {
    font-size: 1.5rem;
    color: rgb(16, 16, 16);
    text-align: center;
    padding: 1rem;
    font-weight: 600;
}

.c5-flexItem p {
    text-align: center;
    color: rgb(16, 16, 16);
    font-size: 16px;
}

.c5-flexItem H2 {
    font-size: 1rem;
    letter-spacing: 3px;
    text-align: center;
    color: #048867;
    padding: 1rem;
}

.c5-flexItem2 {
    margin-top: -3rem;
}

.c5-btn {
    text-align: center;
}

@media  screen and (max-width: 940px) {
    .c5-cards {
        flex-wrap: wrap;

    }

    .c5-flexItem {
        width: 100%;
        padding: 1rem;
    }

    .c5-flexItem2 {
        margin-top: 0;
    }

}
