.component3 {
    width: 100vw;
    height: auto;
    background-color: rgb(244, 239, 239);
    padding: 3rem  0;
}

.c3-header h1{
    font-size: 2.5rem;
    text-align: center;
    color: rgb(27, 88, 210);
    font-weight: bold;
}

.c3-header p {
    text-align: center;
    font-size: 22px;
    padding: 1rem 0;
}

.c3-img {

    width: 800px;
    padding: 1rem;
    transition: all .5s ease-in-out;
}

.c3-img img {
    width: 100%;  
}

.c3-img img:hover {
    transform: scale(1.1);
    transition: all 1s ease-in-out;

}