.component6-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
}


.component6-left {
    width: 400px;
    margin: auto;
    padding: 1rem;
}

.component6-left img {
    width: 100%;
}


.component6-right {
    width: 60%;
    margin: auto;
    padding: 1rem;
}

.component6-right h1 {
    color: rgb(29, 27, 27);
    padding: 1rem 0;
}

.component6-right h2 {
    text-transform: uppercase;
    font-size: 1rem;
    letter-spacing: 1px;
}

.component6-btn {
    padding: 1rem 0;
}

.component6-btn button {
    border-radius: 5px;
}