.component4 {
    background-color: #F4FCFA;
    padding: 3rem 0;
    margin: 3rem 0;
}

.c4-flexbox {
    display: flex;
    /* flex-wrap: wrap; */
    background-color: rgb(242, 236, 227);
    padding: 3rem;
    gap: .5rem;
}

.c4-flexItem {
    background-color: rgba(45, 44, 44, 0.673);
    padding: 1rem;
    width: 280px;
    height: 250px;
}

.flexItem1 {
    background-color: rgb(117, 116, 114);
    padding: 1rem;
    width: 500px;
    height: 250px;
}



.flexIcon {
    font-size: 2.5rem;
    color: rgb(200, 112, 17);
    text-align: center;
}

.c4-flexItem h1 {
    font-size: 1rem;
    color: rgb(255, 255, 255);
    text-align: center;
    padding: 1rem;
}

.c4-flexItem p {
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 12px;
}

.c4-flexItem H2 {
    font-size: .8rem;
    letter-spacing: 3px;
    text-align: center;
    color: rgb(255, 255, 255);
    padding: 1rem;
}


@media  screen and (max-width: 940px) {
    .c4-flexbox {
        flex-wrap: wrap;
    }

    .c4-flexItem {
        width: 100%;
        height: 200px;
    }

    .flexItem1 {
        height: 150px;
    }

}